import S from 'fluent-json-schema'
import {
  ClientEntity,
  Locales,
  Page,
  PageSearchQuery,
  sClientEntity,
  sLocales,
  sPageSearchQuery,
} from '../core'
import { OPERATORS_ROUTES_KEY } from './operator.const'

/**
 * Client Chain interface
 */
export interface Operator extends OperatorData, ClientEntity {}

export function sOperator(isPartial?: boolean) {
  return S.object()
    .id('#operator')
    .additionalProperties(false)
    .extend(sClientEntity(isPartial))
    .extend(sOperatorData(isPartial))
}

export interface OperatorData {
  name: string
  code: string
  type: OperatorType
  description?: Locales<string>
  shortDescription?: Locales<string>
  caption?: Locales<string>
  rating?: string
  location?: string
  color?: string
  links?: OperatorLinks
}

export function sOperatorData(isPartial?: boolean) {
  return S.object()
    .additionalProperties(false)
    .prop('name', S.string())
    .description('The name of the operator.')
    .prop('code', S.string())
    .description('The unique code of the operator.')
    .prop('type', sOperatorType())
    .description('The operator type.')
    .prop('description', sLocales())
    .description('The operator description')
    .prop('shortDescription', sLocales())
    .description('The operator short description')
    .prop('caption', sLocales())
    .description('A short label about the operator')
    .prop('rating', S.string())
    .description('The operator rating')
    .prop('location', S.string())
    .description('The operator location')
    .prop('color', S.string())
    .description('The operator main color')
    .prop('links', sOperatorLinks())
    .description('Links about operator')
    .required(isPartial ? [] : ['name', 'code', 'type'])
}

export interface OperatorLinks {
  website?: string
  twitter?: string
  linkedIn?: string
  crunchbase?: string
}

export function sOperatorLinks() {
  return S.object()
    .additionalProperties(false)
    .prop('website', S.string())
    .prop('twitter', S.string())
    .prop('linkedIn', S.string())
    .prop('crunchbase', S.string())
}

export type OperatorType = 'PROTOCOL' | 'BORROWER' | 'CURATOR'

export function sOperatorType() {
  return S.string().enum(['PROTOCOL', 'BORROWER', 'CURATOR'])
}

export enum OperatorErrorCodes {
  notFound = 'OPERATOR_NOT_FOUND',
  notDeletable = 'OPERATOR_NOT_DELETABLE',
}

export type OperatorFields =
  | '_id'
  | 'name'
  | 'code'
  | 'type'
  | 'description'
  | 'label'
  | 'rating'
  | 'location'
  | 'links'
  | 'createdAt'
  | 'createdBy'
  | 'updatedAt'
  | 'updatedBy'

export const OPERATOR_FIELDS = [
  '_id',
  'name',
  'code',
  'type',
  'description',
  'label',
  'rating',
  'location',
  'links',
  'createdAt',
  'createdBy',
  'updatedAt',
  'updatedBy',
]

export const OPERATOR_SORT_FIELDS = ['_id', 'name']

export interface OperatorsSearchQuery
  extends PageSearchQuery<'name', OperatorFields> {
  name?: string
  'name:ct'?: string
  code?: string
}

export function sOperatorsSearchQuery() {
  return S.object()
    .additionalProperties(false)
    .prop('name', S.string())
    .description('Name of the operator that must match.')
    .prop('name:ct', S.string())
    .description('Name of the operator that must be contained.')
    .prop('code', S.string())
    .description('Code of the operator that must match.')
    .extend(sPageSearchQuery(OPERATOR_FIELDS, OPERATOR_SORT_FIELDS))
}

export interface OperatorsClientModel {
  search: (params?: OperatorsSearchQuery) => Promise<Page<Operator>>
  list: (params?: OperatorsSearchQuery) => Promise<Operator[]>
  findOne: (params?: OperatorsSearchQuery) => Promise<Operator | undefined>
}

export enum OperatorRoutes {
  v1Create = `v1/${OPERATORS_ROUTES_KEY}`,
  v1Delete = `v1/${OPERATORS_ROUTES_KEY}/:operatorId`,
  v1Read = `v1/${OPERATORS_ROUTES_KEY}/:operatorId`,
  v1Update = `v1/${OPERATORS_ROUTES_KEY}/:operatorId`,
  v1Search = `v1/${OPERATORS_ROUTES_KEY}`,
}
